video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}

.main-container {
  background: url('./../images/bushland.jpg') center center/cover no-repeat;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
}

.main-container > h1 {
  color: #e4f1e7;
  font-size: 100px;
  text-align: center;
}

.main-container > p {
  color: #e4f1e7;
  text-align: center;
  font-size: 32px;
}

.main-btns {
  margin-top: 32px;
}

.main-btns .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}

.main-container > img {
  margin-top: -20px;
  height: 200px;
}

@media screen and (max-width: 960px) {
  .main-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
}

@media screen and (max-width: 768px) {
  .main-container > h1 {
    font-size: 50px;
    margin-top: -100px;
  }

  .main-container > p {
    font-size: 30px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
}
